<template>
	<view class="release">
		<!-- 自定义导航 -->
		<u-navbar title="发布需求" @rightClick="rightClick" :autoBack="true" bgColor="#F7BC05"
			titleStyle="color:#fff;font-size: 31rpx;">
			<view class="u-nav-slot" slot="left"
				style="width:57rpx;height:57rpx;text-align: center;line-height: 57rpx;border:1px solid #fff;border-radius: 50%;">

				<u-icon name="arrow-left" size="19" color="#fff"
					custom-style="display: inline-block;line-height: 57rpx;margin:0 auto;left:-2rpx;"></u-icon>
			</view>
		</u-navbar>
		<view class="cont" :style="{top:marginTop+tabMargin+'px',height:contHeight+'px'}">
			<view class="cont_head flex flex_around" :style="{height:releTopHead+'px',lineHeight:releTopHead+'px'}">
				<view class="flex">
					<view class="step flex" :class="currentStep==1?'active':''" @click="changeStep(1)">
						<view class="num">01</view>
						<view class="text">需求内容</view>
					</view>
					<image :src="imgUrl+'/broker/v2/ic_step.png'" mode="widthFix"></image>
					<view class="step flex" :class="currentStep==2?'active':''" @click="changeStep(2)">
						<view class="num">02</view>
						<view class="text">用工信息</view>
					</view>
				</view>
			</view>
			<view class="cont_mid">
				<view class="box" v-show="currentStep == 1" :style="{paddingTop:tabMargin+'px'}">
					<view class="box_in" :style="{height:boxInHeight+'px',padding:releTitHeight+'px'}">
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>工种类别
							</view>
							<view class="input_wrap" :style="{height:subTabHeight+'px'}">
								<uni-data-picker :localdata="data" popup-title="工种类型" @change="onchange"
									@nodeclick="onnodeclick"></uni-data-picker>
							</view>
						</view>
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>工作地址
							</view>
							<view class="input_wrap" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}"
								@click="openMap">
								<input type="text" v-model="address" :placeholder="addrPlace" />
							</view>
						</view>
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>需求描述
							</view>
							<view class="input_wrap" :style="{height:textareaHeight+'px',padding:0}">
								<textarea v-model="content" :style="{height:textareaHeight+'px'}"
									placeholder="请在这里输入您的工作内容以及招聘要求" maxlength="1000" />
							</view>
						</view>
					</view>
					<view class="btn btn_y"
						:style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px',bottom:btnBottom+'px'}"
						@click="changeStep(2)">下一步
					</view>
				</view>
				<view class="box" v-show="currentStep == 2" :style="{paddingTop:tabMargin+'px'}">
					<view class="box_in" :style="{height:boxInHeight+'px',padding:releTitHeight+'px'}">
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>用工人数
							</view>
							<view class="input_wrap" :style="{height:subTabHeight+'px'}">
								<input type="text" v-model="need_count" placeholder="请输入您要招聘的人数" />
								<view class="clear" v-show="need_count" @click="clear('need_count')">
									<image :src="imgUrl+'/broker/ic_circle_ye.png'" mode="widthFix"></image>
								</view>
							</view>
						</view>
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>用工单价
							</view>
							<view class="input_wrap" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">
								<input type="text" v-model="unit_price" placeholder="请输入每个工人的用工单价,并在下方选择单位" />
								<view class="clear" v-show="unit_price" @click="clear('unit_price')">
									<image :src="imgUrl+'/broker/ic_circle_ye.png'" mode="widthFix"></image>
								</view>
							</view>
							<view class="input_wrap flex flex_around"
								:style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px',marginTop:tabMargin+'px'}">
								<u-radio-group v-model="price_type" placement="row" @change="groupChange">
									<u-radio :customStyle="{margin: '0 69rpx'}" activeColor="#F7BC05" labelSize="12"
										v-for="(item, index) in priceTypeList" :key="index" :label="item.name"
										:name="item.value" @change="radioChange">
									</u-radio>
								</u-radio-group>
							</view>
						</view>
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>用工周期
							</view>
							<view class="input_wrap flex flex_around"
								:style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px',color:start_time?'#333':'#888'}"
								@click="openTime">
								<view v-if="start_time" class="line">{{start_time}}</view>
								<view v-if="start_time" class="line mark">至</view>
								<view v-if="start_time" class="line">{{end_time}}</view>
								{{start_time?"":"请选择用工日期范围"}}
								<u-calendar :show="show" mode="range" color="#F7BC05" :closeOnClickOverlay="true"
									@confirm="confirmTime" @close="closeTime"></u-calendar>
							</view>
						</view>
						<view class="in_item" :style="{marginBottom:releTitHeight+'px'}"
							v-show="price_type == 'hourly'">
							<view class="in_tit" :style="{height:releTitHeight+'px',marginBottom:tabMargin/2+'px'}">
								<text>*</text>每日工作时长
							</view>
							<view class="input_wrap" :style="{height:subTabHeight+'px'}">
								<input type="text" v-model="work_time" placeholder="请输入每天工作的时长" />
								<view class="clear" v-show="work_time" @click="clear('work_time')">
									<image :src="imgUrl+'/broker/ic_circle_ye.png'" mode="widthFix"></image>
								</view>
							</view>
						</view>
					</view>
					<view class="btn flex flex_btween" :style="{bottom:btnBottom+'px'}">
						<view class="btn_w" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}"
							@click="changeStep(1)">上一步
						</view>
						<view class="btn_y" :style="{height:subTabHeight+'px',lineHeight:subTabHeight+'px'}">发布需求
						</view>
					</view>


				</view>
			</view>

		</view>
	</view>
</template>

<script>
	const app = getApp()
	export default {
		data() {
			return {
				imgUrl: app.globalData.baseImageUrl,
				marginTop: app.globalData.marginTop,
				tabMargin: app.globalData.tabMargin,
				releTitHeight: app.globalData.releTitHeight,
				subTabHeight: app.globalData.subTabHeight,
				releTopHead: app.globalData.releTopHead,
				btnBottom: app.globalData.btnBottom,
				textareaHeight: 0,
				boxInHeight: 0,
				currentStep: 2,
				contHeight: 0,
				need_count: "",
				unit_price: "",
				show: false,
				price_type: "hourly",
				work_time: "",
				priceTypeList: [{
					name: "元/时",
					value: "hourly"
				}, {
					name: "元/天",
					value: "daily"
				}],
				address: "",
				start_time: "",
				end_time: "",
				addrPlace: "请选择工作地址",
				data: [{
						value: "0093",
						text: "连锁酒店",
						children: [{
								value: "9834",
								text: "保洁"
							},
							{
								value: "9334",
								text: "前台"
							}
						]
					},
					{
						value: "1111",
						text: "商圈餐饮",
						children: [{
								value: "1112",
								text: "服务员"
							},
							{
								value: "1113",
								text: "跑堂"
							}
						]
					}
				]
			}
		},
		onLoad(param) {
			this.id = param.id
			uni.setNavigationBarColor({
				frontColor: '#000000',
				backgroundColor: 'transparent'
			})
			this.contHeight = app.globalData.systemHeight - this.marginTop - this.tabMargin
			this.boxInHeight = this.contHeight - this.releTopHead - this.tabMargin - 2 * this.btnBottom - this.subTabHeight
			this.textareaHeight = this.boxInHeight - 7 * this.releTitHeight - 2 * this.subTabHeight - 1.5 * this.tabMargin
		},
		methods: {
			confirmTime(e) {
				console.log(e)
				this.start_time = e[0]
				this.end_time = e[1]
				this.show = false
			},
			changeStep(index) {
				this.currentStep = index
			},
			clear(key) {
				this.$data[key] = ""
			},
			openTime() {
				this.show = true
			},
			closeTime() {
				this.show = false
			},
			openMap() {
				let _this = this
				uni.chooseLocation({
					success(res) {
						console.log(res)
						_this.address = res.address
						console.log('位置名称：' + res.name);
						console.log('详细地址：' + res.address);
						console.log('纬度：' + res.latitude);
						console.log('经度：' + res.longitude);
					},
					fail(err) {
						console.log(err)
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	::v-deep {
		.u-calendar-month__days__day__select__buttom-info {
			bottom: -10px !important;
		}

		.input-value,
		.uni-data-tree,
		.uni-data-tree>view {
			height: 100% !important;
			padding: 0 !important;
		}

		.input-value-border {
			border: none !important;
		}

		.input-arrow {
			border-left: 1px solid #F7BC05 !important;
			border-bottom: 1px solid #F7BC05 !important;
		}

		.u-radio-group {
			flex: 0 !important;
		}
	}



	.release {
		height: 100vh;
		background: #F7F8FA;
		color: $base-fontcolor;

		.btn {
			position: fixed;
			width: calc(100% - 92rpx);
			left: 46rpx;

			.btn_y,
			.btn_w {
				width: 48%;
			}
		}

		.cont {
			position: fixed;
			left: 0;
			width: 100%;
			overflow-y: scroll;

			.cont_mid {
				padding: 0 46rpx;

				.box {
					.box_in {
						box-sizing: border-box;
						background: #FFFFFF;
						box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.15);
						border-radius: 15rpx;

						.in_item {
							.in_tit {
								font-weight: 400;
								font-size: 27rpx;
								color: #5A5A5A;

								text {
									font-weight: 600;
									color: #DB0000;
								}
							}

							.input_wrap {
								background: #F9F9F9;
								border-radius: 8rpx;
								padding: 0 38rpx 0 19rpx;
								font-size: 12px;
								position: relative;

								.clear {
									position: absolute;
									top: 50%;
									right: 0rpx;
									transform: translateY(-50%);
									padding: 30rpx;
									z-index: 99;

									image {
										width: 38rpx;
									}
								}

								input {
									height: 100%;
								}

								textarea {
									padding: 23rpx 26rpx 38rpx 19rpx;
									box-sizing: border-box;
									font-weight: 400;
									color: #9E9E9E;
									line-height: 38rpx;
								}

								.line {
									width: 33%;
									text-align: center;

									&.mark {
										color: #777;
									}

								}
							}
						}
					}
				}
			}

			.cont_head {
				background: #FFFFFF;

				.step {
					&.active {
						.num {
							background: #F7BC05;
							color: #fff;
						}

						.text {
							font-weight: 600;
							color: #F7BC05;
						}

					}

					.num {
						width: 50rpx;
						height: 50rpx;
						line-height: 50rpx;
						text-align: center;
						background: rgba(247, 188, 5, .1);
						font-weight: 600;
						font-size: 31rpx;
						color: #F7BC05;
						border-radius: 50%;
						margin-right: 10rpx;
					}

					.text {
						font-weight: 400;
						font-size: 29rpx;
						color: #333333;
					}
				}

				image {
					width: 38rpx;
					margin: 0 67rpx;
				}
			}
		}
	}
</style>